import React from 'react';
import QueueAnim from 'rc-queue-anim';
import { Icon, Spin } from 'antd';
import { injectIntl } from 'react-intl';
import { withRouter } from 'react-router-dom';

import { H2C, ButtonCancel } from './../PaySuccess/style';
import { userGetProcessByUuid } from './../../services';
import functions from './../../utils/functions';

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

class PayCancel extends React.Component {
  state = { process: null, loading: true };

  componentDidMount = () => {
    this.fetchProcess();
  };

  fetchProcess = async () => {
    const {
      dashboard,
      history,
      match: { params },
    } = this.props;

    if (params && params.uuid) {
      const response = await userGetProcessByUuid(params.uuid);

      if (response.data) {
        this.setState({
          process: response.data,
        });
      } else {
        if (dashboard) {
          history.push('/dashboard/process-services');
        }
      }
    }
    this.setState({
      loading: false,
    });
  };

  goBack = () => {
    const { dashboard, history } = this.props;
    if (dashboard) {
      return history.push('/dashboard/process-services');
    }
    return history.push('/');
  };

  render() {
    const {
      intl: { messages },
    } = this.props;

    const PricingTable1 = () => (
      <article className="pricing-table-v1">
        <header>
          {' '}
          <H2C>
            <Icon type="check-circle" /> {messages.payment.cancel.canceled}
          </H2C>{' '}
        </header>
        <p className="pricing-price">
          {messages.payment.cancel.paymentProcessCanceled.toUpperCase()}
        </p>
        <p className="pricing-price-detail"></p>
        <div className="pricing-plan-details">
          <ul>
            <li>
              <div className="row justify-content-md-center">
                <div className="col-md-6 p-1">
                  <Icon type="check" /> {messages.payment.cancel.filename.toUpperCase()}
                </div>
                <div className="col-md-6 p-1">
                  {this.state.process ? this.state.process.fileName : null}
                </div>
              </div>
            </li>
            <li>
              <div className="row justify-content-md-center">
                <div className="col-md-6 p-1">
                  <Icon type="check" /> {messages.payment.cancel.nameOfProcess.toUpperCase()}
                </div>
                <div className="col-md-6 p-1">
                  {this.state.process ? jsUcfirst(this.state.process.processName) : null}
                </div>
              </div>
            </li>
            <li>
              <div className="row justify-content-md-center">
                <div className="col-md-6 p-1">
                  <Icon type="check" /> {messages.payment.cancel.language.toUpperCase()}
                </div>
                <div className="col-md-6 p-1">
                  {this.state.process ? (
                    <React.Fragment>
                      {this.state.process.engineSource.toUpperCase()}
                      <Icon type="arrow-right" />
                      {this.state.process.engineTarget.toUpperCase()}
                    </React.Fragment>
                  ) : null}
                </div>
              </div>
            </li>
            <li>
              <div className="row justify-content-md-center">
                <div className="col-md-6 p-1">
                  <Icon type="check" /> {messages.payment.cancel.status.toUpperCase()}
                </div>
                <div className="col-md-6 p-1">
                  {this.state.process ? functions.getStatusByText(this.state.process.status) : null}
                </div>
              </div>
            </li>
            <li>
              <div className="row justify-content-md-center">
                <div className="col-md-6 p-1">
                  <Icon type="check" /> {messages.payment.cancel.quotedPrice.toUpperCase()}
                </div>
                <div className="col-md-6 p-1">
                  {this.state.process ? (
                    <React.Fragment>
                      <Icon type="euro" /> &nbsp;
                      {this.state.process.quoteSelected.price.toFixed(2)}
                    </React.Fragment>
                  ) : null}
                </div>
              </div>
            </li>
          </ul>
        </div>
        <footer>
          <ButtonCancel onClick={this.goBack} type="primary" className="btn-cta w-100">
            {messages.default.goBack.toUpperCase()}
          </ButtonCancel>
        </footer>
      </article>
    );

    return (
      <QueueAnim type="bottom" className="ui-animate">
        <Spin size="large" tip="Loading..." spinning={this.state.loading}>
          <div key="1" className="row justify-content-md-center">
            <div className="form-card__body col-md-5 p-1">
              <article className="article">
                <div className="box box-default">
                  <div className="box-body p-4">
                    <section className="form-v1-container">
                      <PricingTable1 />
                    </section>
                  </div>
                </div>
              </article>
            </div>
          </div>
        </Spin>
      </QueueAnim>
    );
  }
}

export default withRouter(injectIntl(PayCancel));
